@import '../../assets/sass/styles.scss';

@media screen and (min-width : 320px) {
    .sec2 {
        width: 80%;
        margin: 50px auto;

        .image {
            position: relative;
            height: 260px;

            img {
                width: 100%;
                position: absolute;
            }
            img.cardDoc {
                top: 3%;
            }
            img:last-child {
                width: 40%;
                left: 30%;
                top: 30%;
            }

        }
    }
}

@media screen and (min-width : 370px) {
    .sec2 {
        .image {
            height: 350px;
        }
    }
    
}